		<h1>Code ASCII</h1>
		<p>L'acronyme ASCII signifie American Standard Code for Information Interchange (Code américain normalisé pour l'échange d'information).</p>
		<ul>
			<li>L'ASCII se code sur 7 bits, soit 128 caractères.</li>
			<li>L'ASCII étendu se code sur 8 bits, soit 256 caractères.</li>
		</ul>

		<h3>Table ASCII</h3>
